@import url('https://fonts.googleapis.com/css2?family=Caveat&family=Dancing+Script&display=swap');

@font-face {
  font-family: "Get Schwifty";
  src: url('./assets/fonts/get_schwifty.ttf') format('truetype');
}

.App {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.App__header {
  background-color: #262c3a;
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 100px;
  overflow: hidden;
}

.App__header__logo {
  width: auto;
  height: 150px;
  object-fit: cover;
}

.App__header__title {
  font-size: 42px;
}

.App__header__score {
  font-size: 24px;
}

.App__content {
  flex: 1;
  background-color: #6a799c;
  overflow-y: auto;
  position: relative;
}

.App__footer {
  background-color: #3e475c;
  font-size: 20px;
  height: 30px;
  text-align: center;
}

.App__footer__link {
  color: white;
  text-decoration: none;
}